import {
    GET_ACCOUNT_INFO_BEGIN,
    GET_ACCOUNT_INFO_SUCCESS,
    GET_ACCOUNT_INFO_FAIL,
    SELECT_PAY_WAY,
    RESET_SELECT_PAY_WAY
} from '../constants/actionTypes';

const defaultState = {
    getAccountInfoBegin: false,
    getAccountInfoSuccess: false,
    errorCode: '',
    errorMessage: ''
}

// GET_ACCOUNT_INFO_SUCCESS 
function handleGetAccountInfoSuccessData(result) {
    const resData = result.responseData;
    let payInfo = {}, accountInfo = {};
    // 
    accountInfo = {
        userType: resData.userType, //0 2
        changeCardFlag: resData.changeCardFlag, //  01
        openPamaAcctUrl: resData.openPamaAcctUrl //
    };
    // 
    payInfo = {
        productName: resData.productName,
        productCode: resData.productCode,
        protocolUrl: resData.protocolUrl, // url
        amount: resData.payAmount,
        orderPlatform: resData.orderPlatform //Id  99919992
    };
    // 
    let payWapList = handlePayWapList(resData);
    return {
        payInfo,
        payWapList,
        accountInfo
    };
}
// 
function handlePayWapList(resData) {
    const { acctDto, bindcardList, wxDto, zfbDto } = resData;
    // 
    let payWapList = [
        {name: 'one', weight: 0},
        {name: 'alipay', weight: 0},
        {name: 'wechat', weight: 0},
        {name: 'bank', weight: 0}
    ], payWayListObj = {};
    // 
    if (acctDto) { // 
        payWayListObj['one'] = {
            enable: +resData.payAmount > +acctDto.availQty ? false : true,
            title: `(${+acctDto.balance})`,
            description: `${+acctDto.availQty} ${(+acctDto.availQty < +resData.payAmount) ? '':''}`,
            // showRecharge: App.IS_NATIVE && (+acctDto.availQty < +resData.payAmount) // 
        }
    }
    if (bindcardList && bindcardList.length > 0) { // 
        payWayListObj['bank'] = {
            enable: true,
            title: '',
            cardList: bindcardList,
            showArrow: true // 
        }
    }
    if (wxDto) { // 
        payWayListObj['wechat'] = {
            enable: true,
            title: '',
            weight: +wxDto.paymentPriority || 0
        }
    }
    if (zfbDto) { // 
        payWayListObj['alipay'] = {
            enable: true,
            title: '',
            weight: +zfbDto.paymentPriority || 0
        }
    }
    // 
    payWapList = payWapList.map((item) => {
        if(payWayListObj[item.name]) {
            item = Object.assign({}, item, payWayListObj[item.name])
        }
        return item;
    }).sort((a, b) => {
        return a.weight > b.weight
    });
    return payWapList;
}

export default function account(state = defaultState, action) {
    switch (action.type) {
        case GET_ACCOUNT_INFO_BEGIN:
            return Object.assign({}, state, {
                getAccountInfoBegin: true
            })
        case GET_ACCOUNT_INFO_SUCCESS:
            /**
             * handleData 
             *   payInfo: 
             *   payWapList: 
             *   accountInfo: 
             */
            const handleData = handleGetAccountInfoSuccessData(action.result);
            return Object.assign({}, state, {
                getAccountInfoBegin: false,
                getAccountInfoSuccess: true,
                ...handleData
            });
        case GET_ACCOUNT_INFO_FAIL:
            return Object.assign({}, state, {
                getAccountInfoBegin: false,
                getAccountInfoSuccess: false,
                errorCode: action.errorCode,
                errorMessage: action.errorMessage
            });
        case SELECT_PAY_WAY: 
            return Object.assign({}, state, { selectPayWay: action.payWay })
        case RESET_SELECT_PAY_WAY:
            return Object.assign({}, state, { selectPayWay: null })
        default:
            return state;
    }
}


