import React from 'react';
import { Link } from 'react-router-dom';

class NavigationBar extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {
        console.log(this.props);
        const { pathname } = this.props;
        return (
            <div className="header">
                <div className="header-container cl-f">
                    <div className="header-logo fl-l">LOGO</div>
                    {/*  */}
                    <ul className="navigation fl-l cl-f">
                        <li className={pathname === '/' ? "selected" : ""}>
                            <Link to="/"></Link>
                        </li>
                        <li className={pathname === '/flash' ? "selected" : ""}>
                            <Link to="/flash">24h</Link>
                        </li>
                        <li className={pathname === '/detail' ? "selected" : ""}>
                            <Link to="/detail"></Link>
                        </li>
                        <li className={pathname === '/encyclopedia' ? "selected" : ""}>
                            <Link to="/encyclopedia"></Link>
                        </li>
                        <li className={pathname === '/navigation' ? "selected" : ""}>
                            <Link to="/navigation"></Link>
                        </li>
                    </ul>
                    {/* // */}
                    <div className="header-right fl-r">
                        <div className="search fl-l">
                            <img src={require('../../assets/images/search.png')}/>
                            <input type="text"/>
                        </div>
                        <div className="login-register fl-l">
                            <span></span>
                            <span></span>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default NavigationBar;


