import React from 'react';
import { Route, Switch } from 'react-router-dom';
import Utils from '../utils';

import Home from '../views/Home';
import Article from '../views/Article';
import Flash from '../views/Flash';
import Detail from '../views/Detail';
import Encyclopedia from '../views/Encyclopedia';
import Navigation from '../views/Navigation';

import { NavigationBar } from '../components';

class Layout extends React.Component {
    componentWillMount() {

    }
    componentDidMount() {
        this.updatePageTitle(this.props.location.pathname);
    }
    componentWillUpdate(nextProps) {
        const oldPathName = this.props.location.pathname;
        const newPathName = nextProps.location.pathname;
        if (oldPathName !== newPathName) {
            this.updatePageTitle(newPathName)
        }

    }
    updatePageTitle(pathname) { //  title
        switch (pathname) {
            case '/':
                Utils.setTitle('')
                break;
            case '/flash':
                Utils.setTitle('24h')
                break;
            case '/detail':
                Utils.setTitle('')
                break;
            case '/encyclopedia':
                Utils.setTitle('')
                break;
            case '/navigation':
                Utils.setTitle('')
                break;
            default:
                Utils.setTitle('')
                break;
        }
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div className="page-container">
                <NavigationBar pathname={pathname}/>
                <div className="view-container">
                    <Switch>
                        <Route exact path="/" component={Home} title="" />
                        <Route path="/article" component={Article} title="" />
                        <Route path="/flash" component={Flash} title="24h" />
                        <Route path="/detail" component={Detail} title="" />
                        <Route path="/encyclopedia" component={Encyclopedia} title="" />
                        <Route path="/navigation" component={Navigation} title="" />
                    </Switch>
                </div>
            </div>
        )
    }
}

export default Layout;


