import React from 'react';

class SideNewsList extends React.Component {
    render() {
        const { title } = this.props;
        return (
            <section className="side-block hot-info">
                <section className="side-block-title">
                    <h6>{title}</h6>
                </section>
                <ul className="hot-info-list">
                    <li className="hot-info-list-item">500</li>
                    <li className="hot-info-list-item">500</li>
                    <li className="hot-info-list-item">500</li>
                </ul>
            </section>
        )
    }
}

export default SideNewsList;


